<template>
  <div class="center mescroll">
    <div class="merchantInfo" @click="toggle">
      <img :src="$utils.dealImg(shopDetail.shopHeader)" alt="">
      <p>{{shopDetail.shopName}}</p>
      <van-icon v-if="merchantList.length > 1" name="arrow-down" />
    </div>
    <div class="top">
      <div class="title">
        <img class="icon" src="@/assets/img/merchantsIcon/icon11.png" alt />
        <p class="text">今日营业额统计</p>
      </div>
      <div class="list">
        <div class="item">
          <p class="num color">{{dataList[0]}}</p>
          <p class="text">待配送</p>
        </div>
        <div class="item">
          <p class="num">{{dataList[1]}}</p>
          <p class="text">已完成</p>
        </div>
        <div class="item">
          <p class="num">{{dataList[2]}}</p>
          <p class="text">营业额</p>
        </div>
      </div>
    </div>
    <div class="mid">
      <div class="list">
        <div class="item" v-for="(item,index) in list" :key="index" :style="(index + 1) % 3 != 0?'border-right:1pt solid #f6f8fa;':''" @click="to(item.router)">
          <img class="icon" :src="item.icon" alt />
          <p class="text">{{item.text}}</p>
        </div>
      </div>
    </div>
    <div class="bottom" @click="toShop">
      预览
    </div>
    <van-popup v-model="show" position="bottom">
      <van-picker show-toolbar value-key="shopName" title="切换店铺" :columns="merchantList" @cancel="show = false" @confirm="onConfirm" />
    </van-popup>
  </div>
</template>
<script>
import { Icon, Popup, Picker } from "vant";
import {
  getmerchantsCenter,
  getMyMerchantsList,
  setDefaultMerchants,
} from "@/api";
export default {
  components: {
    [Icon.name]: Icon,
    [Popup.name]: Popup,
    [Picker.name]: Picker,
  },
  data() {
    return {
      list: [
        {
          icon: require("@/assets/img/merchantsIcon/icon6.png"),
          text: "订单管理",
          router: { name: "merchantsOrderList" },
        },
        {
          icon: require("@/assets/img/merchantsIcon/icon7.png"),
          text: "营收统计",
          router: { name: "merchantsStatistical" },
        },
        {
          icon: require("@/assets/img/merchantsIcon/icon10.png"),
          text: "资金管理",
          router: { name: "moneyManagement" },
        },
        {
          icon: require("@/assets/img/merchantsIcon/icon8.png"),
          text: "分类管理",
          router: { name: "classifyManagement" },
        },
        {
          icon: require("@/assets/img/merchantsIcon/icon9.png"),
          text: "商品管理",
          router: { name: "goodsManagement" },
        },
        {
          icon: require("@/assets/img/merchantsIcon/icon12.png"),
          text: "我的职员",
          router: { name: "employeesManagement" },
        },
        {
          icon: require("@/assets/img/merchantsIcon/icon5.png"),
          text: "店铺管理",
          router: { name: "merchantsManagement" },
        },
      ],
      dataList: [],
      shopDetail: {},
      merchantList: [],
      show: false,
    };
  },
  methods: {
    to(router) {
      this.$router.push(router);
    },
    toShop() {
      this.$router.push({
        name: "merchantsIndex",
        query: {
          shopCode: this.shopDetail.shopCode,
        },
      });
    },
    toggle() {
      if (this.merchantList.length < 2) {
        return;
      }
      this.show = true;
    },
    onConfirm(value) {
      console.log(value);
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      setDefaultMerchants({ shopCode: value.shopCode }).then(res => {
        loading.clear();
        this.show = false;
        if (res.code == 0) {
          this.$toast("切换成功");
          sessionStorage.setItem("shopCode", value.shopCode);
          this.loadData();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getMyMerchantsList().then(res => {
        if (res.code == 0) {
          this.merchantList = res.data.list;
        } else {
          this.$toast(res.msg);
        }
      });
      getmerchantsCenter({
        shopCode: this.$route.query.shopCode,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.dataList = res.data.list;
          this.shopDetail = res.data.shop;
        } else {
          this.$toast(res.msg);
          setTimeout(() => {
            this.$router.back();
          }, 1000);
        }
      });
    },
  },
  mounted() {
    document.title = "商户中心";
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.center {
  background: #fff;
  .merchantInfo {
    display: flex;
    align-items: center;
    padding: 10px;
    border-bottom: 1pt solid #f5f5f5;
    img {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      margin: 0 10px 0 0;
    }
    p {
      line-height: 30px;
      flex: 2;
    }
  }
  .top {
    padding: 10px 0;
    border-bottom: 1pt solid #f6f8fa;
    .title {
      display: flex;
      align-items: center;
      padding: 0 10px;
      .icon {
        width: 30px;
        height: 30px;
        margin-right: 5px;
      }
      .text {
        flex: 2;
        font-size: 15px;
        line-height: 30px;
      }
    }
    .list {
      display: flex;
      align-items: center;
      padding: 20px 0;
      .item {
        flex: 1;
        text-align: center;
        line-height: 30px;
        .num {
          font-size: 20px;
        }
        .color {
          color: #ce1029;
        }
        .text {
          color: #999;
          font-size: 15px;
        }
      }
    }
  }
  .mid {
    padding: 0 0 50px;
    .list {
      .item {
        padding: 20px 0;
        display: inline-block;
        width: calc(100vw / 3);
        text-align: center;
        line-height: 25px;
        border-bottom: 1pt solid #f6f8fa;
        box-sizing: border-box;
        .icon {
          width: 30px;
          height: 30px;
        }
        .text {
          font-size: 14px;
        }
      }
    }
  }
  .bottom {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background: #ce1029;
    color: #fff;
    line-height: 50px;
    text-align: center;
  }
}
</style>